import React, {useEffect, useState} from 'react';
import ImageGallery from 'react-image-gallery';
import "react-image-gallery/styles/css/image-gallery.css";
import s from './blockImages.module.sass'


export const BlockImages = ({slide, imagesArr}) => {
  
  let image = React.createRef()
  useEffect(() => {
    image.slideToIndex(slide)
  }, [slide])
  
  const images = imagesArr.map(photo => {
    return {original: photo, thumbnail: photo}
  });
  
  return (
    <div className={s.content}>
      <div className={s.content__images}>
        <ImageGallery
          sizes={500}
          ref={i => image = i}
          items={images}
          showBullets={true}
          showIndex={false}
          showThumbnails={false}
          lazyLoad={false}
          showPlayButton={false}
          showFullscreenButton={false}
          showNav={false}
          slideDuration={50}
        />
      </div>
    </div>
  );
};

