import React, {useState} from 'react';
import s from './item.module.sass';
//image
import check from '../../../../assets/images/checked-symbol.png'
import img1 from '../../../../assets/images/warm-light.png'
import img2 from '../../../../assets/images/days-light.png'
import img3 from '../../../../assets/images/cold-light.png'

export const Item = ({title, id, active, setActive, setImagesArr}) => {
  
  let classItem = active ? `${s.item} ${s.active}`: s.item
  
  let clickItem = () => {
    if(id === 0) {
      setImagesArr([img1,img1,img1])
    } else if (id === 1) {
      setImagesArr([img2,img2,img2])
    } else if (id === 2) {
      setImagesArr([img3,img3,img3])
    }
    setActive(id)
  }
  
  return (
    <div onClick={() => clickItem()} className={classItem}>
      <div className={s.active}>
        <img src={check}/>
      </div>
      <span className={s.item__text}>{title}</span>
    </div>
  );
}